<template>
    <el-container>
        <el-header>
            修改/删除水库
        </el-header>
        <el-main>
            <el-card shadow="hover">
                <el-table :data="reservoirList">
                    <el-table-column property="name" label="水库名称" width="150"
                                     align="center"></el-table-column>
                    <el-table-column property="location" label="行政区" width="150"
                                     align="center"></el-table-column>
                    <el-table-column label="经度范围"
                                     align="center">
                        <template slot-scope="scope">
                            {{getLocationRangeLng(scope.row)}}
                        </template>
                    </el-table-column>
                    <el-table-column label="纬度范围"
                                     align="center">
                        <template slot-scope="scope">
                            {{getLocationRangeLat(scope.row)}}
                        </template>
                    </el-table-column>
                    <el-table-column
                            fixed="right"
                            label="操作"
                            width="100"
                            align="center">
                        <template slot-scope="scope">
                            <el-button
                                    type="text"
                                    size="small"
                                    style="margin-right: 5px"
                                    @click="handleEditClick(scope.row)">编辑</el-button>
                            <el-popconfirm
                                    title="操作不可复原，确定删除吗？"
                                    icon="el-icon-info"
                                    iconColor="red"
                                    @onConfirm="handleDeleteClick(scope.row.id, scope.$index)"
                            >
                                <el-button slot="reference"
                                           type="text"
                                           size="small">
                                    删除
                                </el-button>
                            </el-popconfirm>

                        </template>
                    </el-table-column>
                </el-table>
            </el-card>

        </el-main>
        <el-dialog title="修改水库记录" :visible.sync="reservoirEditor.visitable" fullscreen>
            <el-form :inline="true" style="margin-top: -30px">
                <el-form-item label="名称" style="margin-left: 50px">
                    <el-input v-model="reservoirEditor.name" style="width: 190px;" disabled/>
                </el-form-item>
            <el-form-item label="行政区">
                <el-select v-model="reservoirEditor.location"
                           placeholder="请选择"
                           filterable>
                    <el-option
                            v-for="item in locationList"
                            :label="item.province"
                            :value="item.province"
                            :key="item.province">
                        {{item.province}}
                    </el-option>
                </el-select>
            </el-form-item>
                <el-button type="success" style="float:right;" @click="handleEditSaveClick">
                    保存
                </el-button>
            </el-form>
            <div style="position: absolute; top:110px; bottom: 0; left: 0; right: 0; display: flex">
                <baidu-map
                        class="radar-map"
                        :scroll-wheel-zoom="true"
                        :center="centerPoint"
                        :zoom="zoom"
                        :continuous-zoom="true"
                        @moving="stepTwoUpdateMapZoom"
                        @moveend="stepTwoUpdateMapZoom"
                        @zoomend="stepTwoUpdateMapZoom"

                >
                    <bm-polygon :path="polygonPath" stroke-color="blue" :stroke-opacity="0.3"
                                :stroke-weight="2"/>
                    <bm-marker
                            v-for="item in polygonPath"
                            :position="{lng: item.lng, lat: item.lat}"
                            :icon="pointer"
                            :key="item.index"
                            dragging
                            @mousedown="changeCurrentMarker(item.index)"
                            @dragging="handleMarkerMove"
                            @dragend="handleMarkerMoveEnd"
                    >
                    </bm-marker>
                    <transition name="el-zoom-in-top">
                        <el-card shadow="hover" v-show="!dragging" class="el-card-step2-info" :style="movingStyle">
                            <div>
                                左上角经度：<span class="el-card-step2-span" v-text="lng_LT"></span>
                            </div>
                            <div>
                                左上角纬度：<span class="el-card-step2-span" v-text="lat_LT"></span>
                            </div>
                            <div>
                                右下角经度：<span class="el-card-step2-span" v-text="lng_RB"></span>
                            </div>
                            <div>
                                右下角纬度：<span class="el-card-step2-span" v-text="lat_RB"></span>
                            </div>

                        </el-card>
                    </transition>
                </baidu-map>
            </div>


        </el-dialog>
    </el-container>
</template>

<script>
    export default {
        name: "EditReservoir",
        data: function () {
            return {
                reservoirList: [],
                reservoirEditor: {
                    id: -1,
                    visitable: false,
                    name: '',
                    location: '',
                    longitudeLeft:'',
                    longitudeRight:'',
                    latitudeLeft:'',
                    latitudeRight:'',
                },
                locationList: [
                    {province: '北京'},
                    {province: '天津'},
                    {province: '河北'},
                    {province: '山西'},
                    {province: '内蒙古'},
                    {province: '辽宁'},
                    {province: '吉林'},
                    {province: '黑龙江'},
                    {province: '上海'},
                    {province: '江苏'},
                    {province: '浙江'},
                    {province: '安徽'},
                    {province: '福建'},
                    {province: '江西'},
                    {province: '山东'},
                    {province: '河南'},
                    {province: '湖北'},
                    {province: '湖南'},
                    {province: '广东'},
                    {province: '广西'},
                    {province: '海南'},
                    {province: '重庆'},
                    {province: '四川'},
                    {province: '贵州'},
                    {province: '云南'},
                    {province: '西藏'},
                    {province: '陕西'},
                    {province: '甘肃'},
                    {province: '青海'},
                    {province: '宁夏'},
                    {province: '新疆'},
                    {province: '台湾'},
                    {province: '香港'},
                    {province: '澳门'}
                ],
                centerPoint: {
                    lng: '',
                    lat: '',
                },
                currentMarkerIndex: '',
                dragging: false,
                moving: false,
                pointer: {
                    url: '/circle.png',
                    size: {width: 20, height: 20},
                },
                polygonPath: [],
                timeout: {},
                zoom: 11,
            }
        },
        computed: {
            movingStyle: function () {
                if (this.moving) {
                    return {
                        opacity: 0.3
                    }
                } else {
                    return {opacity: 1};
                }
            },
            lng_LT: function () {
                return this.polygonPath.length === 0 ? '-' : this.polygonPath[0].lng;
            },
            lat_LT: function () {
                return this.polygonPath.length === 0 ? '-' : this.polygonPath[0].lat;
            },
            lng_RB: function () {
                return this.polygonPath.length === 0 ? '-' : this.polygonPath[2].lng;
            },
            lat_RB: function () {
                return this.polygonPath.length === 0 ? '-' : this.polygonPath[2].lat;
            },
        },
        methods: {
            getLocationRangeLng: function (item) {
                return item.longitudeLeft +
                    '→'+
                    item.longitudeRight;

            },
            getLocationRangeLat: function (item) {
                return item.latitudeRight +
                    ' → '+
                    item.latitudeLeft;

            },
            handleEditClick: function (data) {
                this.reservoirEditor.id = data.id;
                this.reservoirEditor.name = data.name;
                this.reservoirEditor.location = data.location;
                this.reservoirEditor.longitudeLeft = data.longitudeLeft;
                this.reservoirEditor.longitudeRight = data.longitudeRight;
                this.reservoirEditor.latitudeRight = data.latitudeRight;
                this.reservoirEditor.latitudeLeft = data.latitudeLeft;
                const lng = 0.5*data.longitudeLeft+0.5*data.longitudeRight;
                const lat = 0.5*data.latitudeLeft+0.5*data.latitudeRight;
                this.reservoirEditor.visitable = true;
                const self=this;
                setTimeout(()=>{
                    self.centerPoint = {lng: lng, lat: lat};
                    self.polygonPath = [
                        {index: 0, lng: data.longitudeLeft, lat: data.latitudeLeft},
                        {index: 1, lng: data.longitudeRight, lat: data.latitudeLeft},
                        {index: 2, lng: data.longitudeRight, lat: data.latitudeRight},
                        {index: 3, lng: data.longitudeLeft, lat: data.latitudeRight},
                    ];
                },100);
            },
            stepTwoUpdateMapZoom: function(e) {
                const self = this;
                this.moving = true;
                clearTimeout(this.timeout);
                this.timeout = setTimeout(function () {
                    self.moving = false;
                }, 500);
                const c = e.target.getCenter();
                this.centerPoint.lng = c.lng;
                this.centerPoint.lat = c.lat;
                this.zoom = e.target.getZoom()
            },
            changeCurrentMarker: function (index) {
                this.currentMarkerIndex = index;
            },

            handleMarkerMove: function (e) {
                this.dragging = true;
                const path = this.polygonPath;
                const lng = e.point.lng;
                const lat = e.point.lat;
                switch (this.currentMarkerIndex) {
                    case 0: {
                        const lngLimit = path[2].lng;
                        const latLimit = path[2].lat;
                        path[0].lng = Math.min(lng, lngLimit);
                        path[0].lat = Math.max(lat, latLimit);
                        path[1].lat = Math.max(lat, latLimit);
                        path[3].lng = Math.min(lng, lngLimit);
                        break;
                    }
                    case 1: {
                        const lngLimit = path[3].lng;
                        const latLimit = path[3].lat;
                        path[1].lng = Math.max(lng, lngLimit);
                        path[1].lat = Math.max(lat, latLimit);
                        path[0].lat = Math.max(lat, latLimit);
                        path[2].lng = Math.max(lng, lngLimit);
                        break;
                    }
                    case 2: {
                        const lngLimit = path[0].lng;
                        const latLimit = path[0].lat;
                        path[2].lng = Math.max(lng, lngLimit);
                        path[2].lat = Math.min(lat, latLimit);
                        path[3].lat = Math.min(lat, latLimit);
                        path[1].lng = Math.max(lng, lngLimit);
                        break;
                    }
                    case 3: {
                        const lngLimit = path[1].lng;
                        const latLimit = path[1].lat;
                        path[3].lng = Math.min(lng, lngLimit);
                        path[3].lat = Math.min(lat, latLimit);
                        path[2].lat = Math.min(lat, latLimit);
                        path[0].lng = Math.min(lng, lngLimit);
                        break;
                    }
                    default: {
                        break
                    }
                }
            },
            handleMarkerMoveEnd: function () {
                this.dragging = false;
                this.reservoirEditor.longitudeLeft = this.polygonPath[0].lng;
                this.reservoirEditor.latitudeLeft = this.polygonPath[0].lat;
                this.reservoirEditor.longitudeRight = this.polygonPath[2].lng;
                this.reservoirEditor.latitudeRight = this.polygonPath[2].lat;
            },
            handleEditSaveClick: function () {
                let index = -1;
                for (let i = 0; i < this.reservoirList.length; i++) {
                    if (this.reservoirList[i].id === this.reservoirEditor.id) {
                        index = i;
                    }
                }
                this.axios.get('api/data/reservoir-edit',
                    {
                        params: {
                            id: this.reservoirEditor.id,
                            name: this.reservoirEditor.name,
                            location: this.reservoirEditor.location,
                            longitudeLeft: this.reservoirEditor.longitudeLeft,
                            latitudeLeft: this.reservoirEditor.latitudeLeft,
                            longitudeRight: this.reservoirEditor.longitudeRight,
                            latitudeRight: this.reservoirEditor.latitudeRight,
                        },
                    }).then((response) => {
                    if (response.status === 200) {
                        this.$set(this.reservoirList, index, {
                            id: this.reservoirEditor.id,
                            name: this.reservoirEditor.name,
                            location: this.reservoirEditor.location,
                            longitudeLeft: this.reservoirEditor.longitudeLeft,
                            latitudeLeft: this.reservoirEditor.latitudeLeft,
                            longitudeRight: this.reservoirEditor.longitudeRight,
                            latitudeRight: this.reservoirEditor.latitudeRight,
                        });
                        this.reservoirEditor.visitable = false;
                        this.$notify({
                            title: '成功',
                            message: '水库"' + this.reservoirEditor.name + '"修改成功',
                            type: 'success'
                        });
                    } else {
                        console.log("error")
                    }
                });
            },
            handleDeleteClick: function (id, index) {
                this.axios.get('api/data/reservoir-delete', {params: {id: id}}).then((response) => {
                    if (response.status === 200) {
                        this.reservoirList.splice(index, 1);
                        this.$notify({
                            title: '成功',
                            message: '已删除',
                            type: 'success'
                        });
                    }
                })
            },
            getDateStringFromTimestamp: function (timestamp) {
                return new Date(timestamp).toLocaleDateString();
            },
            getArrayFromStringParams: function (str) {
                const strArr = str.split(',');
                const arr = [];
                for (let i = 0; i < strArr.length; i++) {
                    arr.push(strArr[i] * 1);
                }
                return arr;
            },
            getNumberStringFromDoubleString: function (str) {
                const arr = this.getArrayFromStringParams(str);
                let arrString = String();
                for (let i = 0; i < arr.length; i++) {
                    arrString = arrString.concat(arr[i], ', ');
                }
                return arrString.substring(0, arrString.length - 2);
            },
            deleteAble: function (name) {
                return [].indexOf(name) === -1;
            },
        },
        mounted() {
            this.axios.get('/api/reservoirInfo/reservoir').then((response) => {
                if (response.status === 200) {
                    this.reservoirList = response.data;

                } else {
                    console.log("error")
                }
            });
        }
    }
</script>

<style scoped>
    .el-card {
        border: 1px solid #d4d4d4;
        width: 100%;
    }
    .el-card-step2-info {
        position: absolute;
        top: 10px;
        right: 10px;
        margin: 0;
        width: auto;
        transition: .3s;
    }

    .el-card-step2-switch {
        position: absolute;
        top: 10px;
        left: 10px;
        margin: 0;
        width: auto;
        transition: .3s;
    }
    .el-card-step2-info-moving {
        opacity: 0.3;
    }

    .radar-map {
        flex: 1;
        position: relative;
    }
</style>
